			function veclerp( v1, v2, l ) {

				var v = new THREE.Vector3();
				var lm1 = 1 - l;
				v.x = v1.x * l + v2.x * lm1;
				v.y = v1.y * l + v2.y * lm1;
				v.z = v1.z * l + v2.z * lm1;
				return v;

			}

